@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

.main {
    height: 800px;
    background-size: cover;
    background-repeat: repeat-y;
    background-position: center center;
    background-attachment: fixed;
}
.container{
    max-width: 1200px;
    margin: 0 auto;
}

.main-block{
    position: relative;
    text-align: center;
    padding-top: 200px;
}
.cart-sale_img{
    height: 200px;
    width: 300px;
    border-radius: 10px;

}
.cart-sale{
    margin-top: 20px;
    display: flex;
    background: white;
    width: 40%;
    margin-left: 33%;
    border-radius: 10px;
    border: 3px solid #ffdd00;
    animation: spin 2s linear infinite;
}
.cart-sale_text{
    font-family: 'Press Start 2P', system-ui;
    font-size: 30px;
    color: #ffdd00;
    text-transform: uppercase;
    letter-spacing: 2px;
}
.description{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

@keyframes spin {
    0% { box-shadow: 0 0 0 0 rgba(255, 221, 0, 0.9); }
    100% { box-shadow: 0 0 0 10px rgba(255, 221, 0, 0); }
}

.description button {
    background-color: #ffdd00;
    font-weight: 600;
    font-size: 20px;
    border: 2px dashed #f6f5f5;
}

.cart-sale:hover{
    box-shadow: 5px 5px 8px #ffdd00;

}

.button-left, .button-right{
    font-family: 'Press Start 2P', system-ui;
    background-color: #ffdd00;
    font-size: 30px;
}
.button-left{
    position: absolute;
    top: 314px;
    left: 307px;
}
.button-right{
    position: absolute;
    top: 314px;
    right: 227px;
}