.footer {
	background-color: #4c4c4c;
	color: #fff;
	padding: 40px 0;
}

.container {
	width: 90%;
	margin: 0 auto;
}

.footer-content {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

.footer-column {
	flex: 1;
	margin-bottom: 20px;
}

.footer-column h3 {
	margin-bottom: 10px;
}

.subscribe-form {
	display: flex;
}

.subscribe-form input {
	flex: 2;
	padding: 10px;
	margin-right: 10px;
	border: none;
	border-radius: 5px;
}

.subscribe-form button {
	flex: 1;
	background-color: #ffdd00;
	color: #333;
	border: none;
	border-radius: 5px;
	cursor: pointer;
}

.footer-bottom {
	margin-top: 20px;
	text-align: center;
}
