.cart-container {
    max-width: 600px;
    margin: 0 auto;
    min-height: 700px;
}

.cart-header {
    font-size: 24px;
    margin-bottom: 20px;
}

.cart-items {
    list-style: none;
    padding: 0;
}

.cart-item {
    border-bottom: 1px solid #ccc;
    padding: 10px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.cart-item-name {
    flex-grow: 1;
}

.cart-item-price {
    margin-left: 20px;
}

.cart-item button {
    background-color: #ff0000;
    color: #fff;
    border: none;
    padding: 5px 10px;
    cursor: pointer;
    border-radius: 4px;
}

.cart-item button:hover {
    background-color: #cc0000;
}

.empty-cart-message {
    font-style: italic;
    margin-top: 20px;
}

.clear-cart-button {
    margin-top: 20px;
    background-color: #ffdd00;
    color: #333;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 4px;
}

.clear-cart-button:hover {
    background-color: #7e7c7c;
}
