.container_catalog{
    padding-bottom: 50px;
}
.cart {
    list-style: none;
    padding: 0;
    margin: 0;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-gap: 20px;
}

.cart li {
    display: flex;
    flex-direction: column;
    border: 1px solid #ccc;
    border-radius: 8px;
    overflow: hidden;
    transition: box-shadow 0.3s ease;
}

.cart li:hover {
    box-shadow: 13px 10px 7px rgba(0, 0, 0, 0.1);
    border: 1px solid #ffdd00;
}

.cart li img {
    width: 100%;
    height: 400px;
    object-fit: contain;
    border-bottom: 1px solid #ccc;
}

.cart li .description {
    flex-grow: 1;
    padding: 20px;
    text-align: center;
}

.cart li button {
    background-color: #ffdd00;
    color: #fff;
    border: none;
    border-radius: 0;
    padding: 10px 0;
    cursor: pointer;
    transition: background-color 0.3s ease;
    width: 100%;
    margin-top: auto;
    font-weight: 600;
    font-size: 20px;
}

.cart li button:hover {
    background-color: #ffdd00;
}
.price{
    font-weight: 600;
    font-size: 20px;
}
.texth2{
    font-family: 'Press Start 2P', system-ui;
    font-size: 40px;
    color: #ffdd00;
    text-transform: uppercase;
    letter-spacing: 2px;
    text-align: center;
    margin: 50px;
}
