
header {
    position: sticky;
    top: 0;
    background: rgba(0, 0, 0, 0.7);
    z-index: 1000;
    padding: 15px;
}

nav ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

nav ul li {
    display: inline;
    margin-right: 10px;
}

nav ul li:hover{
    text-shadow: 2px 2px 4px #ffdd00;
}
nav ul li:last-child {
    margin-right: 0;
}

/* Стили для ссылок в навигационном меню */
nav ul li a {
    text-decoration: none;
    color: #fdfcfc;
    font-weight: 600;
    font-size: 21px;
}


nav ul li span {
    background-color: #ffda33;
    color: #faf8f8;
    padding: 5px 10px;
    border: 1px solid black;
}

.search-wrapper {
    position: relative;
}

.search-button {
    position: absolute;
    top: 51%;
    right: -39px;
    transform: translateY(-50%);
    background-color: #ffda33;
    border: none;
    padding: 12px;
    cursor: pointer;
}

.search-input {
    border: 2px solid #ffda33;
    padding: 10px;
}

input:focus-visible {
    outline: 1px solid #171717;
}

.clear-button {
    background-color: #ffda33;
    border: none;
    padding: 12px;
}

.logo-container {
    position: relative;
    width: 50px;
    height: 50px;
}

.circle {
    width: 60%;
    height: 60%;
    border-radius: 50%;
    background-color: #ffdd00;
}


.text {
    font-family: 'Press Start 2P', system-ui;
    font-size: 24px;
    color: #171717;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-58%, -100%);
    letter-spacing: 5px;
}

